                align-items: center;
                margin: 0 40px;
                .Poster{
                  margin: 0;
                  img{
                    max-width: 80%;
                    //margin: 0 auto;

                  ;
                  }
                }
                .Info{
                  &__Content{
                    h1{
                      text-align: center;
                    }
                    &-Header{

                      //
                      .Info__Header-Item:last-child{
                        margin: 0;
                      }
                    }
                    &-Actions{

                      align-items: flex-start;
                      .Mark{
                        margin-bottom: 20px;
                      }
                      .Action{
                        &__List{
                          margin: 0;
                        }
                      }
                    }
                    &-Text{
                      .Btn-Trailer{
                        display: flex;
                        justify-content: flex-start;
                        .Trailer{

                        }

                      }
                    }
                  }
                }
              }
            }
          }
        }
      }

    }
  }
}
